<template>
    <div>

      <el-carousel height="550px" class="marginTop">
        <el-carousel-item v-for="item in list" :key="item.key">
         <div class="banner" :style="{backgroundImage:'url('+url+item.url+')'}"></div>
        </el-carousel-item>
    </el-carousel>
    </div>
</template>
<script>
export default {
  name: 'banner',
  data: () => ({
    list: [
      {
        url: '/slide1.jpg'
      },
      {
        url: '/slide2.jpg'
      },
      {
        url: '/slide3.jpg'
      }
    ],
    url: 'http://www.qingsu2018.com/banner'
  }),
  created () {
    console.log(this.list)
  }
}
</script>
<style lang="scss" scoped>
.banner {
  width: 100%;
  height: 550px;
  background-position: center;
  background-size: cover;
}
.marginTop{
  margin-top: 100px;
}
</style>



